html {
  font-size: 10px;
}

html body {
  font-weight: normal;
  font-family: $font-primary;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  color: $color-dark;
  font-weight: $font-weight-black;
  font-family: $font-secondary;
}

h1 {
  width: 100%;
  max-width: 1140px;
  margin: 0 auto 3.5rem;
  font-size: 9rem;
  line-height: 105%;
  text-align: center;
}

h4 {
  font-size: 3.2rem;
}

p {
  margin: 0 auto;

  @include font(
    $size: 3rem,
    $weight: $font-weight-light,
    $line-height: 160%,
    $spacing: 0.06rem,
  );
}
